/* slider */
/* body {
  font-family:Inter;
} */

.card {
  width: 300px;
  min-width: 300px;
  height:auto;
  background:#fff;
  border-radius:30px;
  position:relative;
  z-index:10;
  margin:25px;
  min-height:356px;
  cursor:pointer;
  transition: all .25s ease;
  box-shadow: 0px 0px 0px 0px rgba(0,0,0, .08);
}

.card:hover {
  transform:translate(0, -10px);
  box-shadow: 0px 17px 35px 0px rgba(0,0,0,.07);
}

.card h4 {
  position:absolute;
  left: 0;
  top: 0;
  padding: 15px;
}

.card i {
  position:absolute;
  right: 0;
  top: 0;
  padding: 15px;
  font-size:1.4rem;
  line-height:3.2rem;
}

.card .card-text, .card .card-text2 {
  padding: 20px;
}

.slider p {
  font-size: .8rem;
  opacity: .6;
  margin-top: 10px;
}


.card-content {
  display:flex;
  align-items:center;
  justify-content:flex-start;
  width:100%;
  overflow:auto;
  /* padding-top: 100px; */
  padding-left:60px;
  padding-right: 50px;
  scroll-behavior:smooth;
}

.card-content::-webkit-scrollbar {
   height:0px;
}

.card-content:after {
  content:'';
  display:block;
  min-width:20px;
  height:100px;
  position:relative;
}

.btn{
  max-width: 60px !important;
  /* min-width:60px; */
  margin:auto 30px;
  height:60px;
  border-radius:20px;
  background:#fff;
  border:0px;
  outline:none;
  cursor:pointer;
  z-index:9999;
  box-shadow: 0px 0px 0px 0px rgba(0,0,0,.08);
  transition: all .25s ease;
}

.btn:hover{
  box-shadow: 0px 17px 35px 0px rgba(0,0,0,.07);
}

.btn i {
  font-size:1.2rem;
}



.slider {
  position: relative;
  display:flex;
  align-items:center;
  /* justify-content:center; */
  background:rgb(242,243,248);
  width:100%;
  /* height:60%; */
  overflow:hidden;
}

.slider:after {
  content:'';
  left:98px;
  height:70vh;
  position:absolute;
  width:150px;
  z-index:100;
  background: linear-gradient(90deg, rgb(242,243,248) 0%,rgba(242,243,248,0)  100%);
  pointer-events:none;
}

.slider:before {
  content:'';
  right:98px;
  height:70vh;
  position:absolute;
  width:150px;
  z-index:100;
  background: linear-gradient(90deg, rgba(242,243,248,0) 0%,rgba(242,243,248,1)  100%);
  pointer-events:none;
}



